























import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

import { DatePickerOptions, Shortcut } from "element-ui/types/date-picker";

@Component({ name: "MyDatetimeRangePicker" })
export default class MyDatetimeRangePicker extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;

    @Prop({ default: () => [] })
    defaultTime!: string[];

    @Prop({ default: "yyyy-MM-dd HH:mm:ss" })
    format!: string;

    @Prop({ default: "yyyy-MM-dd HH:mm:ss" })
    valueFormat!: string;

    @Prop({ default: false, type: Boolean })
    formerOnly!: boolean;
    @Prop({ default: false, type: Boolean })
    disabled!: boolean;

    @Prop({ default: false, type: Boolean })
    afterToday!: boolean;

    @Prop({ default: false, type: Boolean })
    formerTodayOnly!: boolean;

    @Prop({ default: "", type: String })
    pickerOptions!: String;

    shortcutsList = [
        {
            text: "最近一周",
            onClick(picker: any) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                picker.$emit("pick", [start, end]);
            }
        },
        {
            text: "最近一个月",
            onClick(picker: any) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                picker.$emit("pick", [start, end]);
            }
        },
        {
            text: "最近三个月",
            onClick(picker: any) {
                const end = new Date();
                const start = new Date();
                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                picker.$emit("pick", [start, end]);
            }
        }
    ];
    // pickerOptions 组件映射 map
    pickerOptionsMap = new Map([
        [
            "current-month",
            {
                text: "本月",
                onClick(picker: any) {
                    const end = new Date();
                    const start = new Date(new Date(new Date().setDate(1)).setHours(0, 0, 0, 0));
                    picker.$emit("pick", [start, end]);
                }
            }
        ],
        [
            "last-month",
            {
                text: "上月",
                onClick(picker: any) {
                    let myDate = new Date();
                    let year = myDate.getFullYear();
                    let month: Number | String = myDate.getMonth();
                    if (month === 0) {
                        month = 12;
                        year = year - 1;
                    } else if (month < 10) {
                        month = "0" + month;
                    }
                    var monthDate = new Date(year, month as number, 0);
                    const start = new Date(
                        new Date(new Date(`${year}-${month}-01`).setDate(1)).setHours(0, 0, 0, 0)
                    );
                    const end = new Date(
                        new Date(new Date(`${year}-${month}-${monthDate.getDate()}`)).setHours(
                            0,
                            0,
                            0,
                            0
                        )
                    );
                    picker.$emit("pick", [start, end]);
                }
            }
        ],
        [
            "today",
            {
                text: "今天",
                onClick(picker: any) {
                    const start = new Date(new Date().setHours(0, 0, 0, 0));
                    const end = new Date();
                    picker.$emit("pick", [start, end]);
                }
            }
        ],
        [
            "yesterday",
            {
                text: "昨天",
                onClick(picker: any) {
                    const start = new Date(
                        new Date(new Date().getTime() - 24 * 60 * 60 * 1000).setHours(0, 0, 0, 0)
                    );
                    const end = new Date(
                        new Date(new Date().getTime() - 24 * 60 * 60 * 1000).setHours(
                            23,
                            59,
                            59,
                            59
                        )
                    );
                    picker.$emit("pick", [start, end]);
                }
            }
        ]
    ]);

    get formatePickerOptions() {
        const { formerOnly, formerTodayOnly, afterToday, pickerOptionsMap, pickerOptions } = this;
        const shortcuts = this.pickerOptions.split(",").map(item => {
            return pickerOptionsMap.get(item)!;
        });

        const result: DatePickerOptions = {
            shortcuts: afterToday ? [] : pickerOptions ? shortcuts : this.shortcutsList
        };

        if (formerOnly) {
            result.disabledDate = time => {
                return time.getTime() > Date.now();
            };
        } else if (formerTodayOnly) {
            result.disabledDate = time => {
                return time.getTime() > Date.now() - 86400000;
            };
        } else if (afterToday) {
            result.disabledDate = time => {
                return time.getTime() < Date.now() - 86400000;
            };
        }

        return result;
    }
    get starTime() {
        return (this.currentValue && this.currentValue[0]) || "";
    }
    get endTime() {
        return (this.currentValue && this.currentValue[1]) || "";
    }
}
